@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@1,400;1,500&display=swap);
.CustomFurniture {
  background-color: rgb(54, 49, 49);
  padding-top: 60px;
  height: 300px;
  color: #eee;
}

.CustomFurniture .container .row h1 {
  padding-left: 40px;
  text-transform: capitalize;
}

.CustomFurniture .container .row p {
  margin-left: 400px;
  width: 500px;
  font-size: 18px;
  text-transform: capitalize;
  margin-top: 10px;
}
.CustomFurniture .container .row .card {
  margin: auto;
  margin-left: 10px;
  background-color: rgb(167, 135, 109);
  text-align: center;
  padding: 10px;
  margin-top: 60px;
  height: 240px;
  border: none;
  /* box-shadow: 1px 1px 17px rgba(104, 49, 4, 0.719); */
  /* border-top: 1px solid #eee; */
}
.CustomFurniture .container .row .card h3 {
  background-color: #eee;
  border-radius: 50%;
  padding: 10px;
  color: rgb(51, 38, 38);
  width: 50px;
  height: 50px;
  margin: auto;
  margin-top: 30px;
}

.CustomFurniture .container .row .card h6 {
  padding-bottom: 70px;
}

/* feature proucts */
.FeaturedProducts {
  margin-top: 250px;
  text-align: center;
  background-color: rgb(239, 244, 253);
}
/* h1,p in top of this secion */
.FeaturedProducts .container .row h1 {
  color: rgba(0, 0, 0, 0.815);
  padding-top: 50px;
}
.FeaturedProducts .container .row p {
  font-size: 27px;
}
/* products items */
.FeaturedProducts .container .row .card {
  background-color: transparent;
  height: 320px;
  border: none;
  margin-top: 50px;
}
/* img card */
.FeaturedProducts .container .row .card img {
  object-fit: cover;
  position: relative;
  width: 300px;
  height: 250px;
}
.FeaturedProducts .container .row .card:hover img {
  filter: brightness(0.7);
  transition: all 0.7s;
}
/* deatils of product */
.FeaturedProducts .container .row .card .card-cotaine {
  display: flex;
  justify-content: space-around;
}
.FeaturedProducts .container .row .card .card-cotaine p {
  font-size: 19px;
  margin-top: 10px;
  font-weight: 600;
  margin-left: 10px;
}
.FeaturedProducts .container .row .card .card-cotaine h6 {
  margin-top: 18px;
  color: rgb(206, 110, 32);
  margin-right: 10px;
}

/* shopping card display with hover cart */
.FeaturedProducts .display-div {
  position: absolute;
  left: 180px;
  top: 100px;
  font-size: 30px;
  color: rgb(206, 110, 32);
  opacity: 0;
}
.FeaturedProducts .display-div:hover {
  color: white;
}
.FeaturedProducts .container .row .card:hover .display-div {
  opacity: 1;
}
/* button in bottom of this section */
.FeaturedProducts button {
  width: 230px;
  margin: auto;
  border: none;
  border-bottom: 2px solid rgb(206, 110, 32);
  background-color: transparent;
  /* color: white; */
  margin-top: 40px;
  padding: 5px;
  font-size: 25px;
  color: rgba(0, 0, 0, 0.815);
  margin-bottom: 30px;
}

.FeaturedProducts button:hover {
  color: rgb(206, 110, 32);
}

footer {
  margin-top: 100px;
  background-color: rgb(54, 49, 49);
}

/* copyright */
footer p {
  text-align: center;
  color: rgb(206, 110, 32);
  background-color:white;
  font-size: 23px;
  font-weight: 600;
}
/* icons social */
footer .social {
  text-align: center;
  padding-top: 80px;
  padding-bottom: 40px;
}
footer .social a {
  margin: auto;
  font-size: 23px;
  color: white;
  margin-left: 10px;
  text-align: center;
  background-color: rgb(206, 110, 32);
  border-radius: 50%;
  padding: 10px;
}
/* icons */
footer .social a:first-child:hover {
  background-color: rgb(233, 175, 128);
  color: black;
}
footer .social a:nth-child(2):hover {
  background-color: rgb(243, 215, 192);
  color: black;
}
footer .social a:nth-child(3):hover {
  background-color: rgb(253, 241, 230);
  color: black;
}
footer .social a:nth-child(4):hover {
  background-color: rgb(255, 245, 238);
  color: black;
}

