/* home/products */
.Products h1 {
  background-color: rgb(206, 110, 32);
  color: rgb(243, 233, 226);
  padding: 20px;
  font-size: 34px;
  padding-left: 200px;
  word-spacing: 10px;
  margin-top: 80px;
}
.Products h1 .activeProducts {
  color: rgb(218, 205, 205);
  text-decoration: none;
  font-size: 32px;
}
.Products h1 .activeProducts:hover {
  color: rgb(255, 249, 245);
}

.products-items-categeory {
  margin-top: 80px;
}

/* buttons to display way */
.Products .buttons {
  border-bottom: 2px solid #eee;
  padding-bottom: 20px;
}

.Products .buttons button {
  background: transparent;
  border: 1px solid rgb(206, 110, 32);
  margin-left: 10px;
  color: gray;
}

.Products .buttons h5 {
  color: gray;
  padding-top: 10px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
  width: 100%;
  display: none;
}

/* products */

.cards {
  display: flex;
  flex-flow: row wrap;
}

.cards .card {
  height: 350px;
  margin-top: 20px;
  box-shadow: 1px 1px 12px rgb(224, 223, 223);
  border: none;
padding: 10px;

}
.cards .card img {
  height: 220px;
  position: relative;
}

.cards .card:hover img {
  filter: brightness(0.5);
  transition: all 0.7s;
}
.cards .card .shopping-cart {
  position: absolute;
  left: 135px;
  font-size: 30px;
  top: 80px;
  opacity: 0;
}

.cards .card:hover .shopping-cart {
  opacity: 1;
}

.cards .card .shopping-icon {
  color: rgb(206, 110, 32);
}

.cards .card .shopping-icon:hover {
  color: white;
}

.cards .card .card-contain {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  margin-top: 10px;
  padding: 10px;
}

.cards .card .card-contain p {
  font-size: 22px;
  text-transform: capitalize;
  font-weight: 600;
  color: rgb(206, 110, 32);
}

.cards .card .card-contain h6 {
  margin-top: 8px;
  font-size: 18px;
  color: gray;
}

