  width: 300px;
}

.Cards-Column .card img:hover {
  filter: brightness(0.5);
  transition: all 0.7s;
}

/* shopping card icon */
.shopping-icon-card {
  position: absolute;
  width: 100%;
  font-size: 20px;
}

.shopping-icon-card button {
  background-color: transparent;
  border: none;
  color: rgb(206, 110, 32);
  position: absolute;
  right: 10px;
}

.shopping-icon-card button:hover {
  color: rgb(112, 54, 7);
}

/* icon for detail */
